function Element (tagName, props, children) {
  	if(!(this instanceof Element)){
  		return new Element(tagName, props, children)
  	}
  	this.tagName = tagName
  	this.props = props
  	this.children = children
}


Element.prototype.render = function () {
  var el = document.createElement(this.tagName) 
  var props = this.props
  if(this.children.length < 2){
   	props.class += ' test';
    props['data-index'] = parseInt(Math.random()*100000);
  }
  for (var propName in props) { 
    var propValue = props[propName]
    el.setAttribute(propName, propValue)
  }

  var children = this.children || []

  children.forEach(function (child) {
    var childEl = (child instanceof Element)
      ? child.render() // 如果子节点也是虚拟DOM，递归构建DOM节点
      : document.createTextNode(child) // 如果字符串，只构建文本节点
    el.appendChild(childEl)
  })
  return el
}


const UI_ONE = Element('div', {id: 'widgetModel'}, [
  Element('div', {'class':'flex-middle',}, ['模块1']),
  Element('div', {'class':'flex-middle'}, [
  	Element('div', {'class':'flex-middle'},['模块2']),
  	Element('div', {'class':'flex-middle'},['模块3'])]),
  Element('div', {'class':'flex-size-1 flex-middle'}, ['模块4'])
]);

const UI_TWO = Element('div', {id: 'widgetModel'}, [
  Element('div', {'class':'flex-middle'}, ['模块5']),
  Element('div', {'class':'flex-middle'}, ['模块6']),
  Element('div', {'class':'flex-size-1 flex-middle'}, ['模块8'])
]);



export default {
    ONE:{
      'UI_ONE':UI_ONE,
      name:'ONE',
      image:''
    },
    TWO:{
      'UI_TWO':UI_TWO,
      name:'TWO',
      image:''
    }
}


